.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 90vh;
}
.table {
  border-collapse: separate;
  border-spacing: 0 5px;
}

.table th {
  padding: 10px;
  box-shadow: 5px 5px rgba(6, 8, 104, 0.05);
  background-color: #002147;
  text-align: center;
  color: #ffffff;
  border: 1px solid #ffffff;
}

.table td {
  padding: 10px;
  box-shadow: 5px 5px rgba(6, 8, 104, 0.05);
  background-color: #fff;
  text-align: center;
}
.table th,
.table tbody td {
  border-top: 2px solid #002147;
  border-bottom: 2px solid #002147;
}

.table td:first-child,
.table th:first-child {
  border-radius: 20px 0 0 20px;
  border-left: 2px solid #002147;
}

.table td:last-child,
.table th:last-child {
  border-radius: 0 20px 20px 0;
  border-right: 2px solid #002147;
}
.btnRed {
  color: red;
  margin: 2px;
  padding: 5px 20px;
  border-radius: 15px;
  border: none;
}
.btnRed:hover {
  color: white;
  background-color: red;
  cursor: pointer;
}
.btnEdit {
    color: blue;
    margin: 2px;
    padding: 5px 20px;
    border-radius: 15px;
    border: none;
  }
  .btnEdit:hover {
    color: white;
    background-color: blue;
    cursor: pointer;
  }
.completed {
  text-decoration: line-through;
}
.description {
  text-decoration: none;
}